import icons from "url:../../img/icons.svg";

export default class View {
    _data;

    /**
     * Render the recieved data to the page
     * @param {Object | Object[]} data - data to be rendered on the screen (recipe)
     * @param {boolean} [render=true] - boolean value to determine if rendering should be done here or not
     * @returns {undefined | string} - markup is returned if render = false
     * @author Aakash Chahal
     */
    render(data, render = true) {
        if (!data || (Array.isArray(data) && data.length === 0))
            return this.renderError();

        this._data = data;
        const markup = this._generateMarkup();

        if (!render) return markup;

        this._clear();
        this._parentElement.insertAdjacentHTML("afterbegin", markup);
    }

    _clear() {
        this._parentElement.innerHTML = "";
    }

    update(data) {
        this._data = data;
        const newMarkup = this._generateMarkup();
        const newDOM = document
            .createRange()
            .createContextualFragment(newMarkup);
        const newElements = Array.from(newDOM.querySelectorAll("*"));
        const currElements = Array.from(
            this._parentElement.querySelectorAll("*")
        );
        newElements.forEach((newEl, i) => {
            const currEl = currElements[i];
            // updated elements only
            if (
                !newEl.isEqualNode(currEl) &&
                newEl.firstChild?.nodeValue.trim() != ""
            ) {
                currEl.textContent = newEl.textContent;
            }

            // updating rest of the stuff accordinglly
            if (!newEl.isEqualNode(currEl)) {
                Array.from(newEl.attributes).forEach((attr) =>
                    currEl.setAttribute(attr.name, attr.value)
                );
            }
        });
    }

    renderSpinner() {
        this._parentElement.innerHTML = "";
        const markup = `
            <div class="spinner">
                <svg>
                    <use href="${icons}#icon-loader"></use>
                </svg>
            </div>
        `;

        this._clear();
        this._parentElement.insertAdjacentHTML("afterbegin", markup);
    }

    renderError(message = this._errorMessage) {
        const markup = `
            <div class="error">
                <div>
                    <svg>
                        <use
                            href="${icons}#icon-alert-triangle"
                        ></use>
                    </svg>
                </div>
                <p>${message}</p>
            </div>
        `;

        this._clear();
        this._parentElement.insertAdjacentHTML("afterbegin", markup);
    }

    renderMessage(message = this._message) {
        const markup = `
            <div class="message">
                <div>
                    <svg>
                        <use
                            href="${icons}#icon-smile"
                        ></use>
                    </svg>
                </div>
                <p>${message}</p>
            </div>
        `;

        this._clear();
        this._parentElement.insertAdjacentHTML("afterbegin", markup);
    }
}
